









































.nav-wrapper {
  margin-top: 2rem;
  background: #eeeeee;
}

.nav {
  display: flex;
  margin: 0 -0.5rem 2rem;

  .router-link {
    display: block;
    margin: 0 0.5rem;
    padding: 1rem 0.5rem;
    color: black;
    border-bottom: 3px solid transparent;

    &-exact-active {
      border-bottom-color: black;
    }
  }
}
